.subscription {
  padding: 64px;

  background-color: var(--primary-color);
  background-image: url('../assets/subscribe_bg.png');
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;

  flex-direction: column;
  overflow: hidden;
}

.subscription h2 {

  font-size: 60px;
  font-family: var(--playfair-display);
  font-weight: 600;
  line-height: 100px;

  color: #000;
  text-align: center;
}
@media screen and (max-width: 768px) {
  
  .subscription h2 {
    font-size: 45px;
    line-height: 75px;
    margin: 10px;
  }
  
  
}

.subscription p {
  font-size: 18px;
  font-family: var(--plus-jakarta-sans);
  font-weight: 300;
  line-height: 36px;

  margin-top: 32px;

  color: #000;
}

.subscription__form {
  min-width: 480px;
  margin-top: 40px;

  padding: 14px 14px 14px 24px;

  display: flex;
  gap: 10px;

  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 46px;
}

@media screen and (max-width: 768px) {
  
  .subscription__form {
    min-width: 85vw;
    margin-top: 40px;
  
    padding: 14px 14px 14px 24px;
  
    display: flex;
    gap: 10px;
  
    border: 1px solid rgba(255, 255, 255, 0.5);
    border-radius: 46px;
  }
  
}

.subscription__form input {
  flex: 1;

  font-size:  16px;
  font-weight: 400px;
  font-family: var(--plus-jakarta-sans);

  color: #000;
  opacity: 0.5;

  background: transparent;
  border: none;
  outline: none;
}

.subscription__form input::placeholder {
  color: #000;
  opacity: 0.5;
}

.subscription__form button {
  padding: 20px;

  min-width: 180px;

  font-size: 16px;
  font-weight: 500;
  font-family: var(--plus-jakarta-sans);
  color: #fff;
  text-align: center;

  background: var(--black-400);
  border-radius: 46px;
  border: none;
  outline: none;

  cursor: pointer;
}